import { gql } from "apollo-boost";

/*
 * LEADS
 */

const ALL_LEADS = gql`
  query($LeadFilter: LeadFilter, $Paging: CursorPaging) {
    leads(filter: $LeadFilter, paging: $Paging) {
      pageInfo {
        hasNextPage
        hasPreviousPage
        startCursor
        endCursor
      }
      edges {
        node {
          id
          updated
          email
          json
        }
        cursor
      }
    }
  }
`;

const DELETE_LEADS = gql`
  mutation($deleteManyLeadsInput: DeleteManyLeadsInput!) {
    deleteManyLeads(input: $deleteManyLeadsInput) {
      deletedCount
    }
  }
`;

/*
 * Pricing
 */

const ALL_PRICINGS = gql`
  {
    Plans {
      id
      uid
      name
      description
      prices {
        month {
          id
          unit_amount_hr
        }
        year {
          id
          unit_amount_hr
        }
      }
      features {
        name
        value
      }
    }
  }
`;

const UPDATE_PRICINGS = gql`
  mutation UpdatePricing($plans: [PlanInput!]!) {
    updatePricing(plans: $plans) {
      id
    }
  }
`;

/**
 * ACCOUNTS
 */

export const ALL_ACCOUNTS = gql`
  {
    accounts {
      pageInfo {
        hasNextPage
        hasPreviousPage
        startCursor
        endCursor
      }
      edges {
        node {
          id
          data
        }
      }
    }
  }
`;

export const ADD_ACCOUNT =gql`
mutation createAccount($account: CreateOneAccountInput!){
  createOneAccount(input: $account) {
    id
    data
  }
}
`;

export const UPDATE_ACCOUNT = gql`
  mutation UpdatePricing($plans: [PlanInput!]!) {
    updatePricing(plans: $plans) {
      id
    }
  }
`;

export const ACCOUNT_USERS2 = gql`
query ($AccountUserFilter: AccountUserFilter){
  accountUsers(filter: $AccountUserFilter){
  pageInfo {
      hasNextPage
      hasPreviousPage
      startCursor
      endCursor
    }
      edges {
        node {
          id
          account_id
          user_id
        }
      }
    }
}
`

export const ACCOUNT_USERS = gql`
query ($UserFilter: AccountFilter){
  usersList(filter: $UserFilter){
    id email
    }
}
`;

export { ALL_LEADS, DELETE_LEADS, ALL_PRICINGS, UPDATE_PRICINGS };
