import React, { useCallback, useState, useRef } from "react";
import {
  Button,
  Card,
  Layout,
  Page,
  PageActions,
  ContextualSaveBar,
  Stack,
  Tabs,
  TextField,
  TextStyle,
  ChoiceList,
  ButtonGroup,
  TextContainer,
} from "@shopify/polaris";

import { useQuery, useMutation } from "@apollo/react-hooks";

import { ALL_PRICINGS, UPDATE_PRICINGS } from "../data/queries";
import { plans } from "../data/plans";

const MODEL_FIXED = "fixed";
const MODEL_PERSEAT = "seat";
const YEARLY_15 = "15";
const YEARLY_2MO = "2mo";
const YEARLY_20 = "20";
const YEARLY_CUSTOM = "custom";

function ToggleRevenue({ totalRevenue, onPress }: any) {
  const message = totalRevenue
    ? "Edit how much your users will pay"
    : "This shows how much you'll get, net of transaction fees";
  return (
    <Stack alignment="center">
      <Stack.Item fill>
        <p>{message}</p>
      </Stack.Item>
      <ButtonGroup segmented>
        <Button
          pressed={!totalRevenue}
          onClick={() => {
            onPress(false);
          }}
        >
          Net revenue
        </Button>
        <Button
          pressed={totalRevenue}
          onClick={() => {
            onPress(true);
          }}
        >
          Gross revenue
        </Button>
      </ButtonGroup>
    </Stack>
  );
}

function TabMonthly() {
  return <div />;
}

function TabYearly({ yearlyModel, onChange }: any) {
  return null;
  return (
    <Card.Section>
      <ChoiceList
        title="Pricing per year:"
        choices={[
          { label: "15% discount", value: YEARLY_15 },
          { label: "2-month discount", value: YEARLY_2MO },
          { label: "20% discount", value: YEARLY_20 },
          { label: "Custom prices", value: YEARLY_CUSTOM },
        ]}
        selected={yearlyModel}
        onChange={onChange}
      />
    </Card.Section>
  );
}

function PricingPreview({ priceModel, tabYear, totalRevenue }: any) {
  return (
    <Card title="Preview" sectioned>
      <link
        rel="stylesheet"
        href="https://getbootstrap.com/docs/4.0/dist/css/bootstrap.css"
      />
      <div
        style={{ width: "100%" }}
        dangerouslySetInnerHTML={{
          __html:
            "<style>body, p { font-size: 14px!important; line-height: 2rem; margin: 0; }</style>",
        }}
      />
      <div className="xcontainer">
        <br />
        <div className="card-deck mb-3 text-center">
          {(priceModel.prices || []).map((item: any) => {
            return (
              <div key={item.name} className="card mb-4 box-shadow">
                <div className="card-header">
                  <h4 className="my-0 font-weight-normal">{item.name}</h4>
                </div>
                <div className="card-body">
                  <h1 className="card-title pricing-card-title">
                    {Number(item.price) ? '$' : ''}
                    {tabYear ? item.priceYear : item.price}
                    {Number(item.price) ? <small className="text-muted">/ mo</small> : ''}
                  </h1>
                  <ul className="list-unstyled mt-3 mb-4">
                    {item.features.map((f: any) => {
                      return (
                        <li key={f.name}>
                          {f.name} {f.value}
                        </li>
                      );
                    })}
                  </ul>
                  <button
                    type="button"
                    className="btn btn-lg btn-block btn-outline-primary"
                  >
                    Get started
                  </button>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </Card>
  );
}

function PricingForm({
  priceModel,
  tabYear,
  totalRevenue,
  handleFieldChange,
}: any) {
  let cols: Array<string> = [];
  if (priceModel.model === MODEL_FIXED) {
    cols = [
      "Name",
      // `Price ${!tabYear ? "(month)" : "(year)"}`,
      'Price (month)',
      `${totalRevenue ? "Billed" : "Net"} ${tabYear ? "(year)" : "(month)"}`,
      "User limit",
      "", // Edit,
    ];
  }

  return (
    <Card.Section>
      <PricingHeader cols={cols} />
      {(priceModel.prices || []).map((item: any) => (
        <PricingRow
          key={item.name}
          priceModel={priceModel}
          item={item}
          cols={cols}
          tabYear={tabYear}
          totalRevenue={totalRevenue}
          handleFieldChange={handleFieldChange}
        />
      ))}
    </Card.Section>
  );
}

function PricingHeader({ cols, tabYear }: any) {
  return (
    <div>
      {cols.map((c: string) => (
        <div
          key={c}
          style={{
            display: "inline-block",
            width: `${100 / cols.length}%`,
            marginBottom: 10,
            paddingRight: 15,
          }}
        >
          {c}
        </div>
      ))}
    </div>
  );
}

function PricingRow({
  priceModel,
  item,
  cols,
  tabYear,
  totalRevenue,
  handleFieldChange,
}: any) {
  const { id, name, price, priceYear, limitUsers } = item;

  let priceValue = price;
  let priceYearValue = priceYear * 12;

  if (!totalRevenue) {
    priceValue = Math.round(price * (1 - 0.039)) - 0.3;
    priceYearValue = Math.round(priceYear * 12 * (1 - 0.039)) - 0.3;
  }

  if (priceValue < 0) {
    priceValue = 0;
  }
  if (priceYearValue < 0) {
    priceYearValue = 0;
  }

  const curPrice = tabYear ? priceYear : price;

  const priceField = tabYear ? (
    <TextField
      labelHidden
      label="Price (month)"
      value={`${curPrice}`}
      onChange={(v) => handleFieldChange("year", id, v)}
      disabled={false}
    />
  ) : (
    <TextField
      labelHidden
      label="Price (month)"
      value={`${curPrice}`}
      onChange={(v) => handleFieldChange("month", id, v)}
      disabled={false}
    />
  );

  const billedField = tabYear ? (
    <TextField
      labelHidden
      label="Billed (year)"
      value={`${priceYearValue}`}
      onChange={(v) => handleFieldChange("year", id, v)}
      disabled={true}
    />
  ) : (
    <TextField
      labelHidden
      label="Billed (month)"
      value={`${priceValue}`}
      onChange={(v) => handleFieldChange("month", id, v)}
      disabled={true}
    />
  );

  return (
    <div>
      {cols.map((c: string, j: number) => (
        <div
          key={j}
          style={{
            display: "inline-block",
            width: `${100 / cols.length}%`,
            marginBottom: 10,
            paddingRight: 15,
          }}
        >
          {j === 0 ? (
            <TextField
              labelHidden
              label="Name"
              value={name}
              onChange={(v) => handleFieldChange("name", id, v)}
            />
          ) : null}
          {j === 1 ? priceField : null}
          {j === 2 ? billedField : null}
          {j === 3 ? (
            <TextField
              labelHidden
              label="User limit"
              value={limitUsers}
              placeholder="(unlimited)"
              onChange={(v) => handleFieldChange("limitUsers", id, v)}
            />
          ) : null}
          {j === 4 ? (
            <div style={{ textAlign: "right" }}>
              <Button size="slim" url={`/pricing/plans/${id}`}>
                Edit details
              </Button>
            </div>
          ) : null}
        </div>
      ))}
    </div>
  );
}

function makeGraphQlParam(plan: any) {
  const _p = {
    id: plan.id,
    uid: plan.uid,
    prices: {
      month: {
        id: plan.prices.month.id,
        unit_amount_decimal: "" + plan.prices.month.unit_amount_hr * 100
      },
      year: {
        id: plan.prices.year.id,
        unit_amount_decimal: "" + plan.prices.year.unit_amount_hr * 100 * 12
      }
    },
    features: plan.features.map((f : any) => ({name: f.name, value: f.value}))
  }

  return _p;
}

function PricingTabs({ plans }: any) {
  const [isDirty, setIsDirty] = useState(false);
  const [plansData, setPlansData] = useState(JSON.parse(JSON.stringify(plans)));
  const [updateSettings] = useMutation(UPDATE_PRICINGS);

  const handleSave = useCallback(() => {
    const plansInput = plansData.Plans.map(
      (p : any) => makeGraphQlParam(p)
    );

    updateSettings({
      variables: { plans: plansInput },
    });
    setIsDirty(false);
  }, [plansData]);

  const handleFieldChange = useCallback(
    (field, id, value) => {

      const newPlansData = { ...plansData };
      newPlansData.Plans = plansData.Plans.map((p: any) => {
        if (p.id === id) {
          switch (field) {
            case "month":
              p.prices.month.unit_amount_hr = value;
              p.prices.year.unit_amount_hr = value/12*10; //TODO: fix this!
              break;
            case "year":
              p.prices.year.unit_amount_hr = value;
              p.prices.month.unit_amount_hr = value/10*12; //TODO: fix this!
              break;
            case "name":
              p.name = value;
              break;
            case "limitUsers":
              p.limitUsers = value;
              break;
          }
        }

        return p;
      });

      setPlansData(newPlansData);
      setIsDirty(true);
    },
    [plansData]
  );

  const prices =
    plansData && plansData.Plans
      ? plansData.Plans.map((p: any) => {
          // const { id, name, features } = p;
          const plan = {
            id: p.id,
            name: p.name,
            price: p.prices.month.unit_amount_hr,
            priceId: p.prices.month.id,
            priceYear: p.prices.year.unit_amount_hr,
            priceYearId: p.prices.year.id,
            limitUsers: p.limitUsers || '',
            features: p.features,
          };

          return plan;
        })
      : [];

  // TODO: get this from somewhere
  const priceModelData = {
    model: MODEL_FIXED,
    yearlyModel: [YEARLY_2MO],
    feeFixed: 30,
    feePercent: 4.9,
    prices,
  };

  const [totalRevenue, setTotalRevenue] = useState(true);
  const [tabIndex, setTabIndex] = useState(0);
  const [priceModel, setPriceModel] = useState(priceModelData);

  const handleRevenuePress = useCallback(
    (value) => setTotalRevenue(!!value),
    []
  );
  const handleTabChange = useCallback(
    (selectedTabIndex) => setTabIndex(selectedTabIndex),
    []
  );
  const handleYearlyModelChange = useCallback(
    (value) => {
      setPriceModel({
        ...priceModel,
        yearlyModel: value,
      });
    },
    [priceModelData]
  );

  const tabs = [
    {
      id: "yearly",
      accessibilityLabel: "",
      content: "With Year Discount",
    },
    {
      id: "monthly",
      content: "Monthly",
      accessibilityLabel: "",
    },
  ];
  const tabYear = tabIndex === 0;

  const contextualSaveBarMarkup = isDirty ? (
    <ContextualSaveBar
      message="Unsaved changes"
      saveAction={{
        onAction: handleSave,
      }}
      discardAction={{
        onAction: () => {
          setPlansData(plans);
          setIsDirty(false);
        },
      }}
    />
  ) : null;

  return (
    <div>
      {contextualSaveBarMarkup}
        <Card>
          <Card.Section>
            <ToggleRevenue
              totalRevenue={totalRevenue}
              onPress={handleRevenuePress}
            />
          </Card.Section>
          <Tabs tabs={tabs} selected={tabIndex} onSelect={handleTabChange}>
            {tabYear ? (
              <TabYearly
                yearlyModel={priceModelData.yearlyModel}
                onChange={handleYearlyModelChange}
              />
            ) : null}
          </Tabs>

          <PricingForm
            priceModel={priceModelData}
            tabYear={tabYear}
            totalRevenue={totalRevenue}
            handleFieldChange={handleFieldChange}
          />
        </Card>

        <PricingPreview
          priceModel={priceModelData}
          tabYear={tabYear}
          totalRevenue={totalRevenue}
        ></PricingPreview>
    </div>
  );
}

export default function PricingPage() {
  let { loading, error, data } = useQuery(ALL_PRICINGS);

  if (loading) {
    return <p>Loading...</p>;
  }

  return (
    <Page
      title="Pricing"
      primaryAction={{
        content: "Edit pricing model",
        url: "/pricing/models",
      }}
    >
      <PricingTabs plans={error ? { Plans: plans } : data} />
    </Page>
  );
}
